/**
 * FilterAction configures middleware specified by the 'filters'
 * key in a route declaration.
 * @implements {Action}
 */
class FilterAction {

    constructor(resources) {

        this._resources = resources;

    }

    generate(method, path, route) {

        if (Array.isArray(route.filters))
            return route.filters.map(filter => (typeof filter === 'function') ? filter :
                this._resources.lookup(filter).module);

    }

}

export default FilterAction
